@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
body{
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
}

h1, h2, h3{
  font-weight: 300;
  margin: 30px 0;
}

img{
  width: 100%;
}

a , a:hover{
  color: inherit;
  text-decoration: none;
  
}

.events-page{
    
    align-items: center;
}

.create-btn{
    background-color: #F37F00;
    border-color: #F37F00;
    margin-bottom: 25px;
}

.create-btn:hover, .create-btn:focus  {
    color: #fff;
    background-color: #F37F00;
    border-color: #F37F00;
    box-shadow: none;
    
}

.search-bar {
    margin-top: 2rem;
}
.card-buttons {
    display:flex;
    justify-content: space-between;

}

.join-btn {
    background-color: #F37F00;
    border-color: #F37F00;
    color: white
}

.card-footer{
    display: flex;
    justify-content: flex-end;
}

.card-img-top{
    height: 200px;
    object-fit: cover;
}

.event-card{
    width: 22rem;
    margin-bottom: 20px;
}

.card-col{
    display: flex;
    justify-content: center;
}

.details-btn{
    display: flex;
    justify-content: space-between;
}

.col-details{
    margin: 20px 0;
}
